import { ref, onMounted } from '@vue/composition-api'

export default function useList (fetch) {
  const pageIndex = ref(1)
  const pageSize = ref(10)
  const data = ref([])

  const request = () => {
    fetch().then(res => {
      data.value = res
    })
  }

  onMounted(() => {
    request()
  })

  const refresh = () => {
    request()
  }

  const handlePageChange = (index) => {
    console.log(index)
    pageIndex.value = index
    request()
  }

  return {
    data,
    pageSize,
    pageIndex,
    refresh,
    currentChange: handlePageChange
  }
}
